import React from 'react';

const Users = (props) => {
    return (
        <div>
            {props.usersPage.users.map(t =>
                <div key={t.id}>
                    <div>{t.photoURL}</div>
                    {t.name}
                    {t.followed ?
                        <button onClick={() => props.followed(t.id)}>UnFollow</button> :
                        <button onClick={() => props.unfollowed(t.id)}>Follow</button>}
                    <div>{t.status}</div>
                    <div>{t.location.country}</div>
                    <div>{t.location.city}</div>
                </div>
            )}
        </div>
    )


}
export default Users;